<template>
  <div>
    <ul>
      <li 
      v-for="(item, index) in list" 
      :key="index"
      :class="{active: $route.path.indexOf(item.url) >= 0}"
      @click="handleClick(item)">
        <i :class="`icon el-icon-${item.icon}`"></i>
        {{item.name}}
      </li>
    </ul>
  </div>
</template>

<script>
  import {mapMutations} from 'vuex'
  export default {
    data () {
      return {
        list: [
          {
            name: '新建合同',
            url: '/add',
            icon: 'edit'
          },
          {
            name: '我的合同',
            url: '/my',
            icon: 'document'
          },
          {
            name: '收到的合同',
            url: '/recive',
            icon: 'message'
          },
          {
            name: '草稿箱',
            url: '/draft',
            icon: 'edit-outline'
          },
          {
            name: '帮助信息',
            url: '/help',
            icon: 'info'
          }
        ]
      }
    },
    mounted () {
      // console.log(this.$route)
    },
    methods: {
      ...mapMutations({
        setContractList: 'SET_CONTRACTLIST'
      }),
      handleClick (item) {
        this.$router.push(item.url)
        this.getContractList()
      },
      getContractList () {
        this.$request('simulateCall', 'getContractList').then(res => {
          console.log('合同列表：', res)
          console.table(res)
          try {
            this.setContractList(res)
          } catch (e) {
            console.log(e)
          }
        })
      }
    }
  }
</script>

<style scoped lang="less">
  ul {
    display: flex;
    flex-direction: column;
    padding-top: 40px;
    li {
      height: 40px;
      padding-left: 20px;
      line-height: 40px;
      margin-bottom: 10px;
      color: rgba(255, 255, 255, 0.4);
      cursor: pointer;
      &:hover {
        color: #fff;
      }
      .icon {
        padding-right: 10px;
      }
    }
    .active {
      background: #266ab4;
    }
  }
</style>



