<template>
  <div>
    <el-container class="container1">
      <el-header class="header">
        <img src="@/assets/images/logo.jpg">
        <div class="name">
          <h1>星云电子合同系统</h1>
          <h5>NEBULASELECTRONICCONTRACT</h5>
        </div>
        <div class="user">
          当前用户：{{$store.state.address || '未登录'}}
          <el-button type="success" @click="getContractList" style="margin-left: 10px;" size="small">更新合同列表</el-button>
        </div>
      </el-header>
      <el-container class="container2">
        <el-aside width="200px" class="aside">
          <myMenu></myMenu>
        </el-aside>
        <el-main class="main">
          <router-view></router-view>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>

<script>
import {mapMutations} from 'vuex'
import myMenu from '@/components/menu'
export default {
  components: {
    myMenu
  },
  data () {
    return {}
  },
  mounted () {
    this.toHelp()
  },
  methods: {
    toHelp () {
      if (!localStorage.getItem('help')) {
        this.$confirm('新用户您好，是否查看帮助文档?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning',
          center: true
        }).then(() => {
          this.$router.push('/help')
        })
      }
    },
    ...mapMutations({
      setContractList: 'SET_CONTRACTLIST'
    }),
    getContractList () {
      this.$request('simulateCall', 'getContractList').then(res => {
        console.log('合同列表：', res)
        console.table(res)
        try {
          this.setContractList(res)
          this.$message.success('合同列表信息加载成功！')
        } catch (e) {
          console.log(e)
          this.$message.error('合同信息加载失败，请稍后再试！')
        }
      })
    }
  }
}
</script>

<style scoped lang="less">
  .container1 {
    min-height: 100vh;
    .header {
      display: flex;
      background: #266ab4;
      color: #fff;
      max-width: 100vw;
      box-sizing: border-box;
      .name {
        padding-left: 20px;
        h1 {
          font-size: 26px;
        }
      }
      .user {
        align-self: center;
        flex: 1;
        text-align: right;
        padding-right: 30px;
      }
    }
    .container2 {
      max-width: 100vw;
      box-sizing: border-box;
      .aside {
        background: #242838;
      }
      .main {
        max-width: 100%;
        box-sizing: border-box;
      }
    }
  }
</style>



